import React, { useEffect, useState } from 'react'
import { Box, Button, Carousel, Image, Text, TextInput, Menu, Avatar, Form, Layer, Paragraph } from 'grommet'
import { faTelegram, faWhatsapp } from '@fortawesome/free-brands-svg-icons'
import { faComment, faEdit, faPaw, faTrashAlt } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import axios from "axios";
import API_URL from "../../API_config";
import { Link, useParams } from 'react-router-dom';
import { useHistory } from "react-router-dom";
import DropDownC from '../PetPost/DropDownC';
import reportTy from '../../assets/ty.gif';

export default function ShowPost() {

    const history = useHistory();
    const { id } = useParams()
    const [pet, setPet] = useState({ images: [], comments: [] })
    const [comment, setComment] = useState({})
    const [owner, setOwner] = useState({})
    const [dataLoading, setDataLoading] = useState(false)
    const [reported, setReported] = useState(false)

    useEffect(() => {

        axios.get(`${API_URL}/api/petPost/showPost/${id}/`).then(res => {
            let { petPost } = res.data;
            setPet(petPost)
            setOwner(petPost.owner)
            setDataLoading(true)
        }).catch(err => {
            console.log(err)
        })


    }, []);


    const onEdit = (event) => {
        event.preventDefault();
        localStorage.setItem("selectedPost", JSON.stringify(pet));
        history.push(`/pets/edit/${pet._id}`);
    }

    const onDelete = (event) => {
        event.preventDefault();
        axios.delete(`${API_URL}/api/petPost/delete/${pet._id}/`, { headers: { token: localStorage.jwtToken } }).then(res => {

            history.push(`/pets`);
        }).catch(err => {
            console.log(err)
        })


    }

    const onUpdate = (event) => {
        event.preventDefault();
        axios.put(`${API_URL}/api/petPost/editStatus/${pet._id}/`, { status: !pet.status }, { headers: { token: localStorage.jwtToken } }).then(res => {
            if (res.data.updatedPost) setPet(prevState => ({
                ...prevState, status: res.data.updatedPost.status
            }));
        }).catch(err => {
            console.log(err)
        })


    }

    const onComment = (event) => {
        event.preventDefault();

        axios.post(`${API_URL}/api/petPost/addComment/${pet._id}`, comment, { headers: { token: localStorage.jwtToken } }).then(res => {
            let { newPets } = res.data;

            setPet(newPets)

        }).catch(err => {
            console.log(err)
        })
        setComment("")
    }

    const onReport = (event) => {
        event.preventDefault();
        setReported(true);
        setTimeout(() => {
            setReported(false);
        }, 4000);
    }

    const onCommentDelete = (cid) => {

        axios.delete(`${API_URL}/api/petPost/deleteComment/${cid}/`, { headers: { token: localStorage.jwtToken } }).then(res => {

            let { newPets } = res.data;
            setPet(newPets)

        }).catch(err => {
            console.log(err)
        })


    }


    const onCommentChange = ({ target: { name, value } }) => {

        setComment({ ...comment, [name]: value });

    };



    const petImages = pet.images.map(ele => <Image fit="cover" src={ele} />
    )
    const allComments = pet.comments.map(ele =>
        <div className="MyComment">
            <>
                <Box direction="row" align="center">
                    <Avatar src={ele.commenter.avatar} />
                    <Text wordBreak="break-all" className="pottaOne">⠀{ele.commenter.name}:</Text></Box><Text wordBreak="break-all">⠀⠀⠀⠀⠀{ele.text}</Text>
                {localStorage.userId == JSON.stringify(ele.commenter._id) && <DropDownC deleteComment={() => { onCommentDelete(ele._id) }} />}
                <hr />
            </>
        </div>
    )
    return (
        <>
            {dataLoading && <div className='container-1 marginTop'>


                {reported &&
                    <Layer
                        onEsc={() => setReported(false)}
                        onClickOutside={() => setReported(false)}
                    >
                        <Box
                            justify="center"
                            align="center"
                            border={{ color: '#ff2f01', size: 'medium' }}
                            pad="medium">
                            <Image width="200px" height="200px" src={reportTy} />
                            <Paragraph>Thank you for reporting</Paragraph>
                            <Button color="red" onClick={() => setReported(false)}> Close</Button></Box>

                    </Layer>}
                {/* <======== pet photo box =======> */}
                <div className="box-post-1">
                    <Box
                        round={{ size: "10px" }}
                        background="white"
                        basis="auto"
                        // animation="slideUp"
                        align="center"
                        border={{ color: "#0070C0", size: "medium" }}
                        pad="medium"
                        margin="medium"

                    >
                        <Carousel fill className="pet-img-post">
                            {petImages}
                        </Carousel>

                    </Box>
                </div>
                {/* <======== Pet Details =======> */}
                <div className="box-post-2">
                    <Box
                        round={{ size: "10px" }}
                        background="white"
                        // align="center"
                        border={{ color: "#0070C0", size: "medium" }}
                        pad="medium"
                        margin="medium"
                    >
                        <Box
                            direction="row"
                        >
                            <Box pad="xxsmall" margin="small"> 
                                <Avatar src={owner.avatar} />
                            </Box>
                            <Box justify="center">
                                <Text className="pottaOne inline">⠀{owner.name}</Text>
                            </Box>

                        </Box>
                        <Box
                            round={{ size: "10px" }}
                            border={{ color: "#C70039", size: "medium" }}
                            pad="medium"
                            margin="small"
                        >
                            <Text>
                                <p className="pottaOne inline">Staus:</p> {pet.status ? "Adopted" : "Not Adopted"}
                                <br />
                                <p className="pottaOne inline">Name:</p> {pet.name}
                                <br />
                                <p className="pottaOne inline">Gender:</p> {pet.gender}
                                <br />
                                <p className="pottaOne inline">Age:</p> {pet.age.length == 0 ? "Unkown" : pet.age}
                                <br />
                                <p className="pottaOne inline">Neutered: </p>{pet.neureted ? "Yes" : "No"}
                                <br />
                                <p className="pottaOne inline">Vaccines:</p> {pet.vaccines}
                                <br />
                                <p className="pottaOne inline">Description: </p>{pet.description}



                            </Text>

                            <div className="box-post-2-button">
                                <br />
                                <Box
                                    justify='around'
                                    direction="row"

                                >
                                    {localStorage.userId && <>{
                                        localStorage.userId == JSON.stringify(owner._id) ?
                                            <>
                                                <Button color="#0070C0" onClick={(e) => onEdit(e)} icon={<FontAwesomeIcon icon={faEdit} />} primary label="Edit" />

                                                <Button color="orange" onClick={(e) => onUpdate(e)} icon={<FontAwesomeIcon icon={faPaw} />} primary label="Change Status" />

                                                <Button color="#0070C0" onClick={(e) => onDelete(e)} icon={<FontAwesomeIcon icon={faTrashAlt} />} primary label="Delete" />
                                            </>
                                            :
                                            <Button primary color="#c20000" onClick={(e) => onReport(e)} label="Report" />
                                    }</>}


                                </Box>
                            </div>
                        </Box>
                        <div className="box-post-2-0">
                            {localStorage.userId && <><Box
                                justify='around'
                                direction="row"
                                // pad="medium"
                                margin="xsmall"
                            >
                                <a target="_blank" href={`https://wa.me/${owner.whatsapp}`}>
                                    <Button primary color="#34a843" icon={<FontAwesomeIcon icon={faWhatsapp} />} label="WhatsApp" />
                                    </a>
                                <a target="_blank" href={`https://t.me/${owner.telegram}`}><Button primary color="#238fbe" icon={<FontAwesomeIcon icon={faTelegram} />} label="Telegram" /></a>
                            </Box></>}
                        </div>
                        {/* <========  Comments =======> */}
                        <Box
                            round={{ size: "10px" }}
                            border={{ color: "#00C7A6", size: "medium" }}
                            pad="medium"
                            margin="medium"
                            overflow="hidden"

                        >
                            {allComments}
                            {!pet.comments.length&&<Paragraph>No Comments yet</Paragraph>}

                        </Box>


                        {localStorage.userId &&
                            <Form>
                                <Box direction="row" justify="between">
                                    <TextInput onChange={(e) => onCommentChange(e)} name="text" value={comment}></TextInput>
                                    <Button type="submit" color="#0070C0" onClick={(e) => onComment(e)} primary label="Post" icon={<FontAwesomeIcon icon={faComment} />} />
                                </Box>
                            </Form>}
                    </Box>
                </div>

            </div>}
        </>
    )
}
