import React from 'react'
import { Box, Button, Image, Text } from 'grommet'
import { useHistory } from "react-router-dom";


export default function VetRow({ speciality, name, whatsapp, telegram, id, avatar, }) {
    const history = useHistory();

    const contactWithVet = () => {
        localStorage.setItem("targetUser", JSON.stringify({ _id: id, name: name, avatar: avatar }))
        history.push("/inbox")
    }
    return (
        <Box
            responsive={true}
            justify='between'
            align="center"
            border
            direction="row"
            height="170px"
            overflow="hidden"
        >
  

            {/* Item 1 */}
            <Box margin="medium">
                <Image fit="contain" width="150px" height="150px" src={avatar} />
            </Box>
           

            {/* Item 3 */}
            <Box direction="column" justify="between">
                <Box pad="small">
                    <Text className="pottaOne">{name}</Text>
                </Box>

                {/* <Box pad="small">
                    <Text>{whatsapp}</Text>
                </Box> */}

            </Box>

            <Box>
                <Box pad="small" align="center">
                    <Text className="pottaOne">Specialization</Text>
                    <br />
                    <Text>{speciality}</Text>
                </Box>
            </Box>


            <Box></Box>

            {/* Item 4 */}
            {localStorage.userId && <Box pad="xlarge" justify="center" alignContent="around" >
                <Button primary color="#0070C0" label="Contact" onClick={contactWithVet} />
            </Box>}

        </Box>
    )
}
