import { Button, Header, Menu, Image } from 'grommet'
import React,{useEffect} from 'react'
import { AddCircle, CaretDownFill, ContactInfo, Home, Login, Logout, UserAdd } from 'grommet-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPaw, faSyringe, faStore, faInbox } from '@fortawesome/free-solid-svg-icons'
import { Link, useHistory } from 'react-router-dom'
import petHouse from '../assets/pet-house-100px.png'

export default function NavBar(props) {

    const history = useHistory();
    useEffect(() => {
        
        props.loginCallback();


    }, []);

    const onLogout=()=>{
        localStorage.clear();
        props.loginCallback();
        history.push('/')
    }
    return (
        <div className="header pottaOne">

            <Header background="#76c6ff"  pad="small" justify='around'>

                <Link to='/'>
                    <img className="petHouse" src={petHouse} alt="home" />
                    {/* <Button icon={<Home />} hoverIndicator /> */}
                </Link>

                <Link to='/pets'>
                    <Button primary color="#ff2f01"  icon={<FontAwesomeIcon icon={faPaw} />} label="Pets" hoverIndicator />
                </Link>

                <Link to='/shops'>
                    <Button primary color="#ff2f01" icon={<FontAwesomeIcon icon={faStore} />} label="Shops" hoverIndicator />
                </Link>

                <Link to='/vets'>
                    <Button primary color="#ff2f01" icon={<FontAwesomeIcon icon={faSyringe} />} label="Vets" hoverIndicator />
                </Link>

                {/* <Link to='/inbox'>
                    <Button primary color="#ff2f01" icon={<FontAwesomeIcon icon={faInbox} />} label="Inbox" hoverIndicator />
                </Link> */}

                {props.auth.isLoggedIn?
                <Menu icon={<CaretDownFill />} dropBackground="#ff2f01" label="Account" items={[
                    {
                        label: '⠀Profile',
                        onClick: () => history.push('/profile'),
                        icon: <ContactInfo />,
                    },
                    {
                        label: '⠀Inbox',
                        onClick: () => history.push('/inbox'),
                        icon: <FontAwesomeIcon icon={faInbox} />
                    },
                    {
                        label: '⠀New Ad',
                        onClick: () => history.push('/pets/new'),
                        icon: <FontAwesomeIcon icon={faPaw} />
                    },
                    {
                        label: '⠀Logout',
                        onClick:()=>{onLogout()},
                        icon: <Logout />,
                        // onClick: () => logOut()
                    }]}
                />

                    :
                <Menu icon={<CaretDownFill />} dropBackground="#ff2f01" label="Account" items={[
                    {
                        label: '⠀Signup',
                        onClick: () => history.push('/signup'),
                        icon: <UserAdd />
                    },
                    {
                        label: '⠀Login',
                        onClick: () => history.push('/login'),
                        icon: <Login />
                    }]} />


}
                {/* <Menu label="Account" items={[{ label: 'logout' }]} /> */}


            </Header>

        </div>
    )
}