import { Box, Button, Form, FormField, Layer, Paragraph, TextInput } from 'grommet'
import React, { useState } from 'react'
import { useHistory } from 'react-router-dom'
import axios from "axios"
import API_URL from "../API_config";
export default function Forgot() {

    const history = useHistory()
    const [alert, setAlert] = useState(false);
    const [email, setEmail] = useState(false);

    const onChangeInput = (e) => {
        setEmail(e.target.value)

    }
    const reset = (e) => {
        e.preventDefault();
        axios.post(`${API_URL}/api/users/resetpassword`, { email }).then(response => {
            setAlert(true)
            setTimeout(() => {
                setAlert(false)
                history.push("/")
            }, 5000);
        }).catch(error => {

        })
    }

    return (
        <>
            {alert &&
                <Layer
                    onEsc={() => setAlert(false)}
                    onClickOutside={() => setAlert(false)}
                >
                    <Box
                        justify="center"
                        align="center"
                        border={{ color: '#ff2f01', size: 'medium' }}
                        pad="medium">
                        <Paragraph>a Password Reset Link was sent to your Email.</Paragraph>
                        <Button color="red" onClick={() => setAlert(false)}> Close</Button></Box>

                </Layer>}
            <div className="login">

                <Box
                    justify="around"
                    direction="column"
                    background="white"
                    pad="large"
                    round={{ size: "10px" }}
                    border={{ color: '#0070C0', size: 'medium' }}
                    animation={{
                        type: "fadeIn",
                        duration: 1000,
                        size: "large"
                    }}
                >
                    <Box>
                        <Form>
                            <FormField label="Email">
                                <TextInput
                                    id="text-input-id"
                                    name="email"
                                    onChange={(e) => onChangeInput(e)}
                                />
                            </FormField>

                            <br />
                            <br />
                            <div className="center">
                                <Button type="submit" color="red" label="Reset" onClick={(e) => reset(e)} />

                            </div>
                        </Form>
                    </Box>
                    <br />

                </Box>
            </div>
        </>
    )
}
