import React, { useEffect } from 'react'
import { Box, Button, Image, Text } from 'grommet'
import { useHistory } from 'react-router-dom';
import axios from "axios";
import Pets from './Pets';

export default function Profile(props) {

    const history = useHistory();



    if (localStorage.userId) {
        const { name, email, avatar, whatsapp, telegram, userType, verified, speciality } = props.auth.currentUser

        return (
            <>

                <div className='marginTop'>

                    <div className="center">
                        <h1>Profile</h1>
                    </div>

                    <div className="profileBox">

                        <Box
                            round={{ size: "10px" }}
                            background="white"
                            basis="auto"
                            justify='between'
                            animation="slideUp"
                            align="center"
                            direction="row"
                            height="200px"
                            border={{ color: '#0070C0', size: 'medium' }}
                            // pad="medium"
                            margin="medium"
                            overflow="hidden"

                        >

                            <Box>
                                <Image margin="medium" className="pet-img-size"
                                    fit="cover"
                                    src={avatar}
                                />
                            </Box>

                            <Box direction="column">

                                <Box direction="row" pad="xsmall">
                                    <Text className="pottaOne">Name:⠀</Text>{name}{verified ? " ✔︎" : ""}
                                </Box>

                                {userType == "vet" &&
                                    <>
                                        <Box direction="row" pad="xsmall">
                                            <Text className="pottaOne">Specialization:⠀</Text>{speciality}
                                        </Box>
                                    </>}

                                <Box direction="row" pad="xsmall">
                                    <Text className="pottaOne">WhatsApp:⠀</Text>{whatsapp}
                                </Box>

                                <Box direction="row" pad="xsmall">
                                    <Text className="pottaOne">Telegram:⠀</Text>{telegram}
                                </Box>

                                <Box direction="row" pad="xsmall">
                                    <Text className="pottaOne">Email:⠀</Text>{email}
                                </Box>

                            </Box>

                            <Box></Box>



                            <Box>
                                <Button primary color="#0070C0" label="Edit" onClick={() => history.push('/profile/edit')} />
                            </Box>
                            <Box></Box>
                        </Box>
                    </div>
                </div>

                <div className='profilePets'>
                    <Pets owner={JSON.parse(localStorage.userId)} />
                </div>


            </>
        )
    }
    else {
        return (
            <>
                {history.push('/login')}
            </>
        )
    }

}
