import { Box, Card, CardBody, CardHeader, CheckBoxGroup, Image, Layer, RadioButtonGroup, Select, Text } from "grommet";
import React, { useState, useEffect } from "react";
import axios from "axios";
import API_URL from "../API_config";
import PetCard from "./PetCard";
import { Link } from "react-router-dom";
import loadingData from '../assets/loadingData.gif'
import noData from '../assets/noData.gif'

export default function Pets(props) {
    const [posts, setPosts] = useState([]);
    const [dataLoading, setDataLoading] = useState(false)
    const [viewFilter, setViewFilter] = useState(
        {
            species: "All",
            neutered: "All",
            gender: "All",
            status: "All",
            owner: props.owner ? props.owner : "All"
        }
    )

    useEffect(() => {
        axios.get(`${API_URL}/api/petPost/allPosts`).then((res) => {
            setPosts(res.data.posts);
            setDataLoading(true)
        });

    }, []);

    const handleFilterChange = (e) => {
        if (e.target.name === "status") {
            if (e.option === "Adopted") setViewFilter((prev) => ({ ...prev, [e.target.name]: true }))
            else if (e.option === "Not adopted") setViewFilter((prev) => ({ ...prev, [e.target.name]: false }))
            else setViewFilter((prev) => ({ ...prev, [e.target.name]: e.option }))
        }
        else if (e.target.name == "neutered") {
            if (e.option === "Yes") setViewFilter((prev) => ({ ...prev, [e.target.name]: true }))
            else if (e.option === "No") setViewFilter((prev) => ({ ...prev, [e.target.name]: false }))
            else setViewFilter((prev) => ({ ...prev, [e.target.name]: e.option }))
        }
        else setViewFilter((prev) => ({ ...prev, [e.target.name]: e.option }))
    }

    // for mutiple filters you want to transform this funciton into a filter object (instead of a filter property)

    const filteredPosts = posts && posts.filter(((post) => {
        if (dataLoading) {
            return Object.keys(viewFilter).every((key) => {
                return viewFilter[key] == "All" ? true : post[key] === (viewFilter[key])

            })
        }
    }))


    return (
        <>
            {!dataLoading && <Layer className="loadingLayer" >
                <Image className="loadingPetsCat" src={loadingData} />
            </Layer>}
            <div className="container marginTop ">

                <div className="box-1">
                    <Box
                        round={{ size: "10px" }}
                        background="white"
                        basis="auto"
                        // animation="slideUp"
                        align="center"
                        direction="column"
                        border={{ color: "#0070C0", size: "medium" }}
                    // pad="medium"
                    // margin="medium"
                    // width="250px"
                    // height="100%"
                    >
                        <br />
                        <Text className="pottaOne">Filters</Text>
                        <br />
                        <Box
                            width="60%"
                            align="center"
                        >
                            <Text>Species</Text>
                            <Select
                            
                                placeholder="Select"
                                options={["All", "Cat", "Dog", "Bird", "Aquatic", "Reptile", "Other"]}
                                name="species"
                                onChange={handleFilterChange}
                            />

                            <br />

                            <Text>Neutered</Text>
                            <Select
                                placeholder='Select'
                                options={['All', 'Yes', 'No']}
                                name="neutered"
                                onChange={handleFilterChange}
                            />
                            {/* <RadioButtonGroup
                            name="neutered"
                            options={['All', 'Yes', 'No']}
                            onChange={(e) => onChangeNeutered(e)}
                            value={neutered}
                        /> */}
                            <br />

                            <Text>Gender</Text>
                            <Select
                                placeholder="Select"
                                options={["All", "Male", "Female"]}
                                name="gender"
                                onChange={handleFilterChange}
                            />
                            <br />

                            <Text>Status</Text>
                            <Select
                                placeholder="Select"
                                options={["All", "Adopted", "Not adopted"]}
                                name="status"
                                onChange={handleFilterChange}
                            />
                            <br />
                        </Box>
                    </Box>
                </div>

                <div className="box-2">
                    <Box
                        background="white"
                        align="center"
                        direction="row"
                        wrap={true}
                        border={{ color: "#0070C0", size: "medium" }}
                        pad="medium"
                        justify="around"
                        round={{ size: "10px" }}
                    >

                        {dataLoading && filteredPosts && filteredPosts.reverse().map(post =>
                            <Link to={`/pets/post/${post._id}`} ><PetCard post={post} /></Link>
                        )}

                        {dataLoading && !filteredPosts.length && <><Text>hmmm.. Nothing to see here </Text> <Image src={noData} /> </>}

                        {/*
                          <div style={{ display: "flex", flexWrap: "wrap" }}> </div>
                        */}
                    </Box>


                </div>
            </div>
        </>
    );
}
