import React, { useEffect, useState } from 'react'
import { Avatar, Box, Button, Image, Text, TextInput } from 'grommet'
import InboxUsersSide from './InboxUsersSide'
import axios from "axios";
import API_URL from "../API_config";
import InboxChat from './InboxChat'
import socketIOClient from "socket.io-client";
import { useHistory } from 'react-router-dom';

export default function Inbox(props) {
    const [currentSession, setCurrentSession] = useState(null);
    const [contactList, setContactList] = useState([])
    const [messages, setMessages] = useState([])
    const history = useHistory();
    const [loading, setLoading] = useState(false)

    useEffect(() => {
        var contactArray = []



        axios.get(`${API_URL}/api/users/contactList`, { headers: { token: localStorage.jwtToken } })
            .then((res) => {

                if (localStorage.targetUser) {
                    res.data.contact.push(localStorage.targetUser)
                    localStorage.removeItem("targetUser")

                }

                setContactList(res.data.contact)

            }).catch((err) => {

            })








    }, [])


    const startSession = (user) => {
        var MessagesRecords = []

        if (currentSession != null) {
            currentSession.socket.disconnect();
        }

        const socket = socketIOClient(API_URL);
        setCurrentSession({ socket, id: user })


        socket.emit("userDetails", { from: JSON.parse(localStorage.userId), to: user })
        socket.on('output', (data) => { //recieves the entire chat history upon logging in between two users and displays them
            MessagesRecords = data;
            setMessages(data)

            setLoading(true)
        })

        socket.on('message', (data) => { //recieves the entire chat history upon logging in between two users and displays them

            setMessages(previousState => ([...previousState, data]))

            setLoading(true)
        })



    }


    const sendMessage = (message) => {

        currentSession.socket.emit("chatMessage", { from: JSON.parse(localStorage.userId), to: currentSession.id, text: message.text })

    }


    return (
        <>
            {localStorage.userId ? <div className='marginTop marginBottom'>
                <div className="center">
                    <h1>Inbox</h1>
                </div>

                <div className="chatBox">
                    <Box
                        background="white"
                        basis="auto"
                        animation="slideUp"
                        direction="row"
                        border={{ color: '#0070C0', size: 'large' }}

                    >
                        <InboxUsersSide contactList={contactList} startChat={startSession} />


                        {/* <=============== User Messages Side ===============> */}

                        {loading && <InboxChat messages={messages} sendMessage={sendMessage} />}

                        <Box></Box>

                    </Box>
                </div>
            </div>
                :
                <> { history.push("/login")}</>
            }
        </>
    )
}
