import React, { useState } from 'react'
import { Form, FormField, TextInput, Button, Box, Paragraph, Layer } from 'grommet'

import axios from "axios"
import API_URL from "../API_config";
import { useHistory } from 'react-router-dom'
import { Link, useParams } from 'react-router-dom';

export default function Reset() {
    const history = useHistory();
    const { token } = useParams()

    const [password, setPassword] = useState(false);
    const [alert, setAlert] = useState(false);

    const onChangeInput = (e) => {
        setPassword(e.target.value)
    }


    const reset = (e) => {
        e.preventDefault();
        
        axios.post(`${API_URL}/api/users/reset/${token}`, { password }).then(response => {

            setAlert(true)
            setTimeout(() => {
                setAlert(false)
                history.push("/login")
            }, 5000);
        }).catch(error => {

        })


    }


    return (
        <div className="login">

            <Box
                justify="around"
                direction="column"
                background="white"
                pad="xlarge"
                round={{ size: "10px" }}
                border={{ color: '#0070C0', size: 'medium' }}
                animation={{
                    type: "fadeIn",
                    duration: 1000,
                    size: "large"
                }}
            >
                {alert &&
                    <Layer
                        onEsc={() => setAlert(false)}
                        onClickOutside={() => setAlert(false)}
                    >
                        <Box
                            justify="center"
                            align="center"
                            border={{ color: '#ff2f01', size: 'medium' }}
                            pad="medium">
                            <Paragraph>Your password was successfully changed</Paragraph>
                            <Button color="red" onClick={() => setAlert(false)}> Close</Button></Box>

                    </Layer>}

                <Form>


                    <FormField label="New password">
                        <TextInput
                            placeholder="your password"
                            id="text-input-id"
                            name="password"
                            type='password'
                            onChange={(e) => onChangeInput(e)}
                        />
                    </FormField>
                    <br />
                    <br />
                    <div className="center">
                        <Button type="submit" onClick={(e) => reset(e)} color="#0070C0" primary label="Reset" />
                    </div>
                </Form>
            </Box>
        </div>
    )
}
