import { Box, Button, Image, Text, TextInput, InfiniteScroll, Form } from 'grommet'
import React, { useEffect, useState } from 'react'
import ChatFrom from './Chat/ChatFrom'
import ChatTo from './Chat/ChatTo'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEdit, faPaperPlane } from '@fortawesome/free-solid-svg-icons'

export default function InboxChat(props) {
    const [message, setMessage] = useState({})

    const onTextChange = ({ target: { name, value } }) => {

        setMessage({ ...message, [name]: value });

    };

    const messageComponents = props.messages.map(ele => {
        if (JSON.stringify(ele.from) == localStorage.userId) {
            return <ChatTo text={ele.text} />
        } else {
            return <ChatFrom text={ele.text} />
        }
    })

    const onSendMessage =(event)=>{
        event.preventDefault();
        props.sendMessage(message)

        setMessage("")
    }
    return (
        <div>
            <Box
                border
            // width="500px" height="400px"
            //  fill={true}
            //  overflow="auto"
            >
                {/* <Image
                    fit="cover"
                    src="https://i.pinimg.com/originals/90/c0/aa/90c0aa65f159abfca4837846526f4013.gif"
                />
                <Text>

                    Choose a conversation from the left
                    <br />

                </Text> */}
                <div className="scroll">
                    {/* <Box border direction="column" width="500px" height="400px" overflow="auto"></Box> */}

                
                    {/* <Box>
                    <Box alignSelf="start" background="#76C6FF" border pad="small" margin="small" round={{ size: "10px" }}>
                    <Text wordBreak={true}>Message from Vet</Text>
                    </Box>
                    </Box> */}
                  
                    {messageComponents}
                    {/* <Box>
                    <Box alignSelf="end" background="#0ac200" border pad="small" margin="small" round={{ size: "10px" }}>
                        <Text wordBreak={true} alignSelf="end">Message from User</Text>
                    </Box>
                    </Box> */}

                </div>
                <Form>
                <Box
                    direction="row"
                    pad="small"
                    align="center"
                    >
                    <TextInput placeholder="Enter your message" onChange={(e) => onTextChange(e)} name="text" value={message}></TextInput>
                    <Button type="submit" primary color="#0070C0" icon={<FontAwesomeIcon icon={faPaperPlane} />} label="Send" onClick={(e) => {onSendMessage(e)}} />
                    
                </Box>
                </Form>


            </Box>
        </div>
    )
}
