import React, { useState } from 'react'
import { Form, FormField, TextInput, Button, Box, Paragraph, Layer, Text } from 'grommet'
import { useHistory } from "react-router-dom";
import axios from "axios";
import API_URL from "../API_config";
import jwt_decode from "jwt-decode";
import { Link } from 'grommet-icons';

export default function Login(props) {
    const history = useHistory();
    const [loggedIn, setLoggedIn] = useState(true); // to show aleart
    const [credentials, setCredentials] = useState({ email: "", password: "" });
    const onChangeInput = (event) => {
        const { name, value } = event.target;
        setCredentials({
            ...credentials,
            [name]: value,
        });
    };

    const onSubmit = (event) => {
        event.preventDefault();

        axios
            .post(`${API_URL}/api/users/login`, { credentials: credentials })
            .then((res) => {
                const token = res.data.token;
                const msg = res.data.msg;
                if (token) {
                    localStorage.setItem("jwtToken", token);
                    props.loginCallback();
                    history.push("/");
                } else {
                    setLoggedIn(false);
                    // setTimeout(() => {
                    //     setLoggedIn(true);
                    // }, 4000);
                }
            });
    };


    return (
        <div className="login">

            <Box
                justify="around"
                direction="column"
                background="white"
                pad="large"
                round={{ size: "10px" }}
                border={{ color: '#0070C0', size: 'medium' }}
                animation={{
                    type: "fadeIn",
                    duration: 1000,
                    size: "large"
                }}
            >


                {!loggedIn &&
                    <Layer
                        onEsc={() => setLoggedIn(true)}
                        onClickOutside={() => setLoggedIn(true)}
                    >
                        <Box
                            justify="center"
                            align="center"
                            border={{ color: '#ff2f01', size: 'medium' }}
                            pad="medium">
                            <Paragraph>Email or Password is incorrect</Paragraph>
                            <Button color="red" onClick={() => setLoggedIn(true)}> Close</Button></Box>

                    </Layer>}

                <Box>
                    <Form>
                        <FormField label="Email">
                            <TextInput
                                id="text-input-id"
                                name="email"
                                onChange={(e) => onChangeInput(e)}
                            />
                        </FormField>

                        <FormField label="Password">
                            <TextInput
                                id="text-input-id"
                                name="password"
                                type='password'
                                onChange={(e) => onChangeInput(e)}
                            />
                        </FormField>
                        <br />
                        <br />
                        <div className="center">
                            <Button type="submit" color="#0070C0" primary label="Login" onClick={(e) => onSubmit(e)} />

                        </div>
                    </Form>
                </Box>
                <br />
                <Box direction="column" align="center">
                    <br />
                    <Text>Forgot Your Password?</Text>
                    <br />
                    <Button color="red" label="Reset Here" onClick={() => history.push("/forgotpassword")} />
                </Box>

            </Box>
        </div>
    )
}
