import { Box, Layer, Text } from 'grommet'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import logo from '../assets/logo.png'
import pets from '../assets/pets.png'
import LatestPetCard from './LatestPetCard'
import axios from "axios";
import API_URL from "../API_config";
import Credits from './Credits'

export default function Home() {

    const [posts, setPosts] = useState([]);
    const [dataLoading, setDataLoading] = useState(false)

    useEffect(() => {
        
        axios.get(`${API_URL}/api/petPost/latest`).then((res) => {
            setPosts(res.data.posts);
            setDataLoading(true)
        });

    }, []);


    const allPosts = posts.map(post => { return <Link to={`/pets/post/${post._id}`} ><LatestPetCard post={post} /></Link> })


    return (
        <>
            <div className="marginTop">
                <div className="landingBox">

                    
                    <img className="logo" src={logo} alt="logo" />
    

                    <br />
                    <img className="petLogo" src={pets} alt="logo" />
                    <br /><br />

                    <div className="latestPets">

                        <Box
                            background="white"
                            align="center"
                            direction="row"
                            wrap={true}
                            border={{ color: "#0070C0", size: "medium" }}
                            pad="medium"
                            justify="around"
                            round={{ size: "10px" }}
                        >
                            <Box background="white">
                                <Text
                                    size="25px"
                                    className="pottaOne">Latest Ads
                                </Text>
                            </Box>

                            {dataLoading && allPosts}

                        </Box>
                    </div>

                </div>

            </div>

        </>
    )
}
