import React, { useState } from 'react'
import { Avatar, Box, Button, Form, FormField, Image, Layer, Stack, Text, TextInput } from 'grommet'
import { useHistory } from "react-router-dom";
import axios from "axios";
import API_URL from "../API_config";
import loadingCat from '../assets/loadingCat.gif'

export default function EditProfile(props) {

  const history = useHistory();
  const [updated, setUpdated] = useState(true); // to show aleart
  const [user, setUser] = useState(props.auth.currentUser); // user info
  const [image, setImage] = useState(
    user.avatar
  );
  const [loading, setLoading] = useState(false);

  //const [message, setMessage] = useState("") //for the error message

  const uploadImage = async (e) => {
    const files = e.target.files;
    const data = new FormData();
    data.append("file", files[0]);
    data.append("upload_preset", "pawfive");
    setLoading(true);
    axios
      .post("https://api.cloudinary.com/v1_1/bookwormcloud/image/upload", data)
      .then((res) => {
        const file = res.data;
        setImage(file.secure_url);
        setUser({ ...user, avatar: file.secure_url });
        setLoading(false);

      })
      .catch((err) => {
        console.log(err);
      });
  };

  //to add the input inside user
  const onChangeInput = ({ target: { name, value } }) => {
    setUser({ ...user, [name]: value, avatar: image });
  };

  const onSubmit = (event) => {
    event.preventDefault();
    if (!loading) {

      axios
        .put(`${API_URL}/api/users/editProfile`, { user }, { headers: { token: localStorage.jwtToken } })
        .then((res) => {
          const token = res.data.token;

          if (token) {
            localStorage.jwtToken = token
            props.loginCallback();
            history.push("/profile");
          } else {
            //setMessage('The email is already used by somone else. Please change the email')
            setUpdated(false);
            setTimeout(() => {
              setUpdated(true);
            }, 3000);
          }
        })
        .catch((err) => console.log(err));
    }
  };



  return (
    <div className="signup">

      <Box
        justify="around"
        direction="row"
        background="white"
        pad="medium"
        round={{ size: "10px" }}
        border={{ color: '#0070C0', size: 'medium' }}
        animation={{
          type: "fadeIn",
          duration: 1000,
          size: "large"
        }}
      >
        {/* Box 1 */}
        <Box>

          {loading && <Layer>
            <Avatar src={loadingCat} />
          </Layer>}

          <Form>
            <FormField label="Name">
              <TextInput
                name="name"
                value={user.name}
                onChange={(e) => onChangeInput(e)}
              />
            </FormField>

            <FormField label="Email">
              <TextInput
                name="email"
                value={user.email}
                onChange={(e) => onChangeInput(e)}
              />
            </FormField>


            <FormField label="WhatsApp">
              <TextInput
                name="whatsapp"
                value={user.whatsapp}
                onChange={(e) => onChangeInput(e)}
              />
            </FormField>
          </Form>
        </Box>


        {/* Box 2 */}

        <Box>
          <Form>

            <FormField label="Telegram ID">
              <TextInput
                name="telegram"
                value={user.telegram}
                onChange={(e) => onChangeInput(e)}
              />
            </FormField>
            <br />
            <FormField label="Choose your image">
              <input
                type="file"
                accept="image/png, image/jpeg, image/jpg"
                name="avatar"
                onChange={uploadImage}
              />
            </FormField>

          </Form>


          <Stack anchor="top-right">
            <Avatar src={user.avatar} />
          </Stack>

        </Box>
      </Box>

      <div className="center">
        <br />
        <br />
        <Button
          color="#0070C0"
          type="submit"
          primary label="Update"
          onClick={(e) => onSubmit(e)}
        />
      </div>
    </div>
  )
}
