import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import axios from "axios";
import API_URL from "../API_config";
import { Form, FormField, TextInput, Button, Box, Layer, Avatar, Paragraph, Stack } from "grommet";
import loadingCat from '../assets/loadingCat.gif'

export default function Signup() {
  const history = useHistory();
  //for random image 
  const avatar = ['https://res.cloudinary.com/bookwormcloud/image/upload/v1613337964/pawfive_images/yizruns7fxh1rew40n1m.png',
    'https://res.cloudinary.com/bookwormcloud/image/upload/v1613338068/pawfive_images/u6aydorp74kdq8qr6vny.png',
    'https://res.cloudinary.com/bookwormcloud/image/upload/v1613338124/pawfive_images/ajdw1oaftroognvwp31e.png',
    'https://res.cloudinary.com/bookwormcloud/image/upload/v1613338140/pawfive_images/x9wnykxk0mvj3uhuibdu.png']
  const [register, setRegister] = useState(true); // to show aleart
  const [image, setImage] = useState(
    avatar[random(0, (avatar.length - 1))]
  );
  const [user, setUser] = useState({ avatar: image }); // user info
  const [loading, setLoading] = useState(false);

  const [errorMessage, setErrorMessage] = useState("Make Sure email is not used and all fields are correct");

  function random(min, max) {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }
  const uploadImage = async (e) => {
    const files = e.target.files;
    const data = new FormData();
    data.append("file", files[0]);
    data.append("upload_preset", "pawfive");
    setLoading(true);
    axios
      .post("https://api.cloudinary.com/v1_1/bookwormcloud/image/upload", data)
      .then((res) => {
        const file = res.data;
        setImage(file.secure_url);
        setUser({ ...user, avatar: file.secure_url });
        setLoading(false);

      })
      .catch((err) => {
        console.log(err);
      });
  };

  //to add the input inside user
  const onChangeInput = ({ target: { name, value } }) => {
    setUser({ ...user, [name]: value, avatar: image });
  };

  const onSubmit = (event) => {
    event.preventDefault();
    if (!loading) {

      if (!user.password || user.password.length < 8) {
        setErrorMessage("Make Sure email is not used and all fields are correct. Password must be at least 8 characters.")
        setRegister(false)

      } else {
        axios
          .post(`${API_URL}/api/users/signup`, user)
          .then((res) => {
            const user = res.data.user;

            if (user) {
              history.push("/login");
            } else {
              
              setRegister(false);
              
            }
          })
          .catch((err) => console.log(err));
      }
    }
  };

  return (
    <div className="signup">

      <Box
        justify="around"
        direction="row"
        background="white"
        pad="medium"
        round={{ size: "10px" }}
        border={{ color: '#0070C0', size: 'medium' }}
        animation={{
          type: "fadeIn",
          duration: 1000,
          size: "large"
        }}
      >
        {/* Box 1 */}
        <Box>
          {loading && <Layer>
            <Avatar src={loadingCat} />
          </Layer>}

          {!register &&
            <Layer
              onEsc={() => setRegister(true)}
              onClickOutside={() => setRegister(true)}
            >
              <Box
                justify="center"
                align="center"
                border={{ color: '#ff2f01', size: 'medium' }}
                pad="medium">
                <Paragraph>{errorMessage}</Paragraph>
                <Button color="red" onClick={() => setRegister(true)}> Close</Button></Box>


            </Layer>}

          <Form>
            <FormField label="Name">
              <TextInput
                id="text-input-id"
                name="name"
                onChange={(e) => onChangeInput(e)}
              />
            </FormField>

            <FormField label="Email">
              <TextInput
                id="text-input-id"
                name="email"
                onChange={(e) => onChangeInput(e)}
              />
            </FormField>

            <FormField label="Password">
              <TextInput
                id="text-input-id"
                name="password"
                type="password"
                onChange={(e) => onChangeInput(e)}
              />
            </FormField>
          </Form>
        </Box>


        {/* Box 2 */}

        <Box>
          <Form>
            <FormField label="Phone">
              <TextInput
                type="number"
                id="text-input-id"
                name="whatsapp"
                onChange={(e) => onChangeInput(e)}
              />
            </FormField>

            <FormField label="Telegram ID">
              <TextInput
                id="text-input-id"
                name="telegram"
                onChange={(e) => onChangeInput(e)}
              />
            </FormField>
            <br />
            <FormField label="Choose your image">
              <input
                type="file"
                accept="image/png, image/jpeg, image/jpg"
                name="avatar"
                onChange={uploadImage}
              />
            </FormField>

          </Form>

          <Stack anchor="top-right">
            <Avatar src={user.avatar} />
          </Stack>

        </Box>
      </Box>

      <div className="center">
        <br />
        <br />
        <Button
          color="#0070C0"
          type="submit"
          primary label="Sign Up"
          onClick={(e) => onSubmit(e)}
        />
      </div>

    </div>
  );
}
