import { Box } from 'grommet'
import React from 'react'
import VetRow from './VetRow'
import{useEffect,useState} from 'react'
import axios from "axios"
import API_URL from "../API_config";
export default function Vets() {
    const [vets, setVets] = useState([])

    useEffect(() => {


        axios.get(`${API_URL}/api/vet/all`)
    
          .then((res) => {
            
            setVets(res.data.vets)
    
            
          })
      }, [])

      const allVets=()=>vets.map(({name,whatsapp,telegram,_id,avatar,speciality})=><VetRow 
      name={name}
      whatsapp={whatsapp} 
      avatar={avatar}
      speciality={speciality}
      telegram={telegram}
      id={_id}
      
      />)


    return (
        <div className='marginTop'>
            <div className='center'>
                <h1>Vets</h1>
            </div>

            <div className="shopList">
                <Box
                
                    round={{size: "10px"}}
                    background='white'
                    border={{ color: '#0070C0', size: 'medium' }}
                    animation={{
                        type: "fadeIn",
                        duration: 1000,
                        size: "large"
                    }}
                >

                   {allVets()}

                </Box>
            </div>


        </div>
    )
}
