import React from 'react'
import { Avatar, Box, Button, Image, Text, TextInput } from 'grommet'
import SideUser from './Chat/SideUser'

export default function InboxUsersSide(props) {

    const listOfContact=props.contactList.map((ele)=>{
        var users = JSON.parse(ele)
        return (<SideUser ava={users.avatar} name={users.name} clickEvent={()=>{props.startChat(users._id)}}/>)
    })

    return (
        <div >
            <div className="profileBox">

                <Box
                    background="white"
                    basis="auto"
                    justify='between'
                    // animation="slideUp"
                    align="center"
                    border={{ color: '#FAFAF6', size: 'small' }}
                >
                    <div className="userInboxSide">
                        <Box
                            background="white"
                            width="200px"
                            height="100%"
                            direction="column"
                            border={{ color: "#F6F6F1", size: "small" }}
                            // pad="small"
                        >
                            <div className="usersScroll">
                            {listOfContact}

                            </div>

                        </Box>
                    </div>

                </Box>
            </div>

        </div>
    )
}
