import React, { useState } from 'react'
import { Avatar, Box, Button, Form, FormField, Layer, Paragraph, Select, Stack, Text, TextArea, TextInput } from "grommet";
import { faPaw, faSyringe } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import axios from 'axios';
import API_URL from "../../API_config";
import { useHistory } from 'react-router-dom';
import loadingCat from '../../assets/loadingCat.gif'

export default function NewPost() {
    const history = useHistory();
    const [post, setPost] = useState({ images: [] }); // post info
    const [loading, setLoading] = useState(false);
    const [limit, setLimit] = useState(false); //to show alert if user try to uplaod and images uploaded are 4
    const [created, setCreated] = useState(true);

    const uploadImage = async (e) => {
        //4 is the limit
        if (post.images.length < 4) {
            const files = e.target.files;
            const data = new FormData();
            data.append("file", files[0]);
            data.append("upload_preset", "pawfive");
            setLoading(true);
            axios
                .post("https://api.cloudinary.com/v1_1/bookwormcloud/image/upload", data)
                .then((res) => {
                    const file = res.data;
                    
                    //{ images: [ ...images, file.secure_url ]}
                    setPost(prevState => ({
                        ...prevState, images: [...prevState.images, file.secure_url]
                    }));

                    setLoading(false);

                })
                .catch((err) => {
                    console.log(err);
                });
        }
        else {
            setLimit(true)
        }
    };

    const onChangeInput = ({ target: { name, value } }) => {
        if (name == "vaccines") value = value.length ? value : "None"
        setPost({ ...post, [name]: value });
    };

    const onChangeSelect = ({ target: { name }, option }) => {
        if (option == "Yes") option = true
        else if (option == "No") option = false
        setPost({ ...post, [name]: option });

    };

    const onSubmit = (event) => {
        event.preventDefault();
        if (!loading) {

            axios
                .post(`${API_URL}/api/petPost/add`, { post }, { headers: { token: localStorage.jwtToken } })
                .then((res) => {
                    const newPost = res.data.newPost
                    if (newPost) {
                        history.push(`/pets/post/${newPost._id}`);
                        setCreated(true)
                    }
                    else {
                        setCreated(false)
                    }

                })
                .catch((err) => console.log(err));
        }
    };

    const removeImage = (event, src) => {
        event.preventDefault();
        var array = [...post.images]; // make a separate copy of the array
        var index = array.indexOf(src)
        if (index !== -1) {
            array.splice(index, 1);
            setPost(prevState => ({
                ...prevState, images: array
            }));
        }
    }

    const petImages = post.images.map(ele =>
        <Stack anchor="top-right">
            <Avatar src={ele} />
            <Box
                onClick={(e) => removeImage(e, ele)}
                background="red"
                pad={{ horizontal: 'xsmall' }}
                round
            >
                <Text color="white">×</Text>
            </Box>
        </Stack>
    )



    if (localStorage.userId) {
        return (
            <div className="newPostBox">
                <Box
                    border={{ color: '#0070C0', size: 'medium' }}
                    background="white"
                    // align="center"
                    direction="row"
                    pad="medium"
                    justify="around"
                    round={{ size: "10px" }}
                    animation={{
                        type: "fadeIn",
                        duration: 1000,
                        size: "large"
                    }}
                >
                    <Box>
                        {loading && <Layer>
                            <Avatar src={loadingCat} />
                        </Layer>}

                        {limit &&
                            <Layer
                                onEsc={() => setLimit(false)}
                                onClickOutside={() => setLimit(false)}
                            >
                                <Box
                                    justify="center"
                                    align="center"
                                    border={{ color: '#ff2f01', size: 'medium' }}
                                    pad="medium">
                                    <Paragraph>You can only upload 4 images maximum</Paragraph>
                                    <Button color="red" onClick={() => setLimit(false)}> Close</Button></Box>

                            </Layer>}

                        {!created &&
                            <Layer
                                onEsc={() => setCreated(true)}
                                onClickOutside={() => setCreated(true)}
                            >
                                <Box
                                    justify="center"
                                    align="center"
                                    border={{ color: '#ff2f01', size: 'medium' }}
                                    pad="medium">
                                    <Paragraph>Please make sure all fields are filled in correctly</Paragraph>
                                    <Button color="red" onClick={() => setCreated(true)}> Close</Button></Box>

                            </Layer>}

                        <Form>
                            <FormField label="Pet Name">
                                <TextInput
                                    name="name"
                                    onChange={(e) => onChangeInput(e)}
                                />
                            </FormField>

                            <FormField label="Age">
                                <TextInput
                                    name="age"
                                    // type="number"
                                    onChange={(e) => onChangeInput(e)}
                                />
                            </FormField>

                            <FormField label="Gender">
                                <Select
                                    placeholder="Gender"
                                    options={["Male", "Female"]}
                                    name="gender"
                                    onChange={(e) => onChangeSelect(e)}
                                />
                            </FormField>
                        </Form>
                    </Box>

                    <Box>
                        <Form>
                            <FormField label="Select Species">
                                <Select
                                    placeholder="Species"
                                    options={["Cat", "Dog", "Bird", "Aquatic", "Reptile", "Other"]}
                                    name="species"
                                    onChange={(e) => onChangeSelect(e)}
                                />
                            </FormField>

                            <FormField label="Neuter Status">
                                <Select
                                    placeholder="Neutered?"
                                    options={['Yes', 'No']}
                                    name="neutered"
                                    onChange={(e) => onChangeSelect(e)}
                                />
                            </FormField>

                            <FormField label="Vaccines (Separate by commas)">
                                <TextInput
                                    name="vaccines"
                                    onChange={(e) => onChangeInput(e)}
                                />
                            </FormField>
                        </Form>
                    </Box>

                    <Box>
                        <Form>
                            <FormField label="Location">
                                <TextInput
                                    name="location"
                                    onChange={(e) => onChangeInput(e)}
                                />
                            </FormField>

                            <FormField label="Description">
                                <TextArea
                                    name="description"
                                    onChange={(e) => onChangeInput(e)}
                                />
                            </FormField>

                            <FormField label="Photo">
                                <input
                                    type="file"
                                    accept="image/png, image/jpeg, image/jpg"
                                    name="photo"
                                    onChange={uploadImage}
                                />
                            </FormField>
                        </Form>
                        {petImages}
                    </Box>

                </Box>
                <br />
                <br />

                <div className="center marginBottom">
                    <Button onClick={(e) => onSubmit(e)} primary color="#0070C0" label="Post Adoption Ad" icon={<FontAwesomeIcon icon={faPaw} />} />
                </div>

            </div>
        )
    }

    else {
        return (
            <>
                {history.push('/login')}
            </>
        )
    }
}
